<template>
  <div class="w-50 mx-auto">
    <div class="d-flex align-items-center">
      <h4>  </h4>
      <button type="button" class="btn btn-danger ml-auto" @click="showModal"> </button>
    </div>
    <form class="pt-3" @submit.prevent="updateRealtor(); backToList()">
      <div class="form-group">
        <label for="lastName"></label>
        <input type="text" class="form-control" id="lastName" v-model="realtorData.lastName" placeholder="  " required>
      </div>
      <div class="form-group pt-1">
        <label for="firstName"></label>
        <input type="text" class="form-control" id="firstName" v-model="realtorData.firstName" placeholder="  " required>
      </div>
      <div class="form-group pt-1">
        <label for="division"></label>
          <model-select :options="divisions" :selected-option="realtorData.division" v-model="realtorData.division" placeholder="  "></model-select>
      </div>
      <div class="form-group pt-1">
        <label for="registerDate"> </label>
        <datepicker input-class="form-control bg-light" language="ru" format="dd.MM.yyyy"
          monday-first placeholder="  " v-model="realtorData.registerDate"></datepicker>
      </div>
      <div class="d-flex pt-3">
        <button type="submit" class="btn btn-success"></button>
        <button type="button" class="btn ml-auto" @click="backToList()"></button>
      </div>
    </form>
    <modal v-if="isModalVisible" @close="closeModal"></modal>
  </div>
</template>

<script>
import Modal from './Modal.vue'

export default {
  name: 'edit',
  data: () => ({
    divisions: [
      {value: '1', text:''},
      {value: '2', text:''},
      {value: '3', text:''},
      {value: '4', text:''},
      {value: '5', text:''}
    ],
    isModalVisible: false
  }),
  mounted() {
    this.$store.dispatch('getRealtor', this.$route.params.realtorId)
  },
  components: {
    Modal
  },
  computed: {
    realtorData () {
      return this.$store.state.choosenRealtor
    }
  },
  methods: {
    updateRealtor() {
      this.$store.dispatch('updateRealtor', this.realtorData)
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    }
  }
}
</script>

<style scoped>
</style>



