<template>
  <div class="w-50 mx-auto">
    <h4>  </h4>
    <form class="pt-3" @submit.prevent="createRealtor(); backToList()">
      <div class="form-group">
        <label for="lastName"></label>
        <input type="text" class="form-control" id="lastName" v-model="newRealtorData.lastName" placeholder="  " required>
      </div>
      <div class="form-group pt-1">
        <label for="firstName"></label>
        <input type="text" class="form-control" id="firstName" v-model="newRealtorData.firstName" placeholder="  " required>
      </div>
      <div class="form-group pt-1">
        <label for="division"></label>
        <model-select :options="divisions" v-model="newRealtorData.division" placeholder="  "></model-select>
      </div>
      <div class="form-group pt-1">
        <label for="registerDate"> </label>
        <datepicker input-class="form-control bg-light" language="ru" format="dd.MM.yyyy"
          monday-first placeholder="  " v-model="newRealtorData.registerDate"></datepicker>
      </div>
      <div class="d-flex pt-3">
        <button type="submit" class="btn btn-success"></button>
        <button type="button" class="btn ml-auto" @click="backToList()"></button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'create',
  data: () => ({
    newRealtorData: {
      lastName: '',
      firstName: '',
      division: '',
      registerDate: new Date()
    },
    divisions: [
      {value: '1', text:''},
      {value: '2', text:''},
      {value: '3', text:''},
      {value: '4', text:''},
      {value: '5', text:''}
    ]
  }),
  methods: {
    createRealtor() {
      this.$store.dispatch('createRealtor', this.newRealtorData)
    }
  }
}
</script>

<style scoped>
</style>



